import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { Btc } from './btc';

@Injectable({
  providedIn: 'root'
})
export class BtcPriceService {
  static ENDPOINT = 'https://api.coindesk.com/v1/bpi/currentprice/btc.json';

  constructor(protected httpClient: HttpClient) { }

  currentPrice() {
    return this.httpClient
      .get(BtcPriceService.ENDPOINT)
      .pipe(
        map((data: any) => new Btc().deserialize(data.bpi.USD.rate_float))
      );
  }
}
