import { ConversionService } from 'src/app/data/conversion.service';
import { Exchange } from './../../../../data/exchange';
import { Observable } from 'rxjs/internal/Observable';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-exchange-selector',
  templateUrl: './exchange-selector.component.html',
  styleUrls: ['./exchange-selector.component.scss']
})
export class ExchangeSelectorComponent implements OnInit {
  exchanges$: Observable<Exchange[]>;
  constructor(private conversionService: ConversionService) {}

  ngOnInit() {
    this.exchanges$ = this.conversionService.exchanges$;
  }

  resetSources() {
    this.conversionService.selectedExchanges$.next(
      this.conversionService.exchanges$.value
    );
  }

  toggleExchange(exchange: Exchange) {
    if (this.isSelectedExchange(exchange)) {
      if (this.enableToggle()) {
        const values = this.conversionService.selectedExchanges$.value.filter(
          _ => _.id !== exchange.id
        );
        this.conversionService.selectedExchanges$.next(values);
      }
    } else {
      const values = [
        ...this.conversionService.selectedExchanges$.value,
        exchange
      ];
      this.conversionService.selectedExchanges$.next(values);
    }
  }

  isSelectedExchange(exchange): boolean {
    return !!this.conversionService.selectedExchanges$.value.find(
      _ => _.id === exchange.id
    );
  }

  enableToggle(): boolean {
    return this.conversionService.selectedExchanges$.value.length > 1;
  }
}
