import { Injectable } from '@angular/core';
import { Btc } from './btc';
import { BtcPriceService } from './btc-price.service';
import { CurrencyService } from './currency.service';
import { ExchangeService } from './exchange.service';
import { Exchange } from './exchange';
import { Currency } from './currency';
import BigNumber from 'bignumber.js';
import { BehaviorSubject, forkJoin } from 'rxjs';
import { delay, repeat, tap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class ConversionService {
  static BTC_TO_SATOSHI = 100000000.0;

  // Defines if the user updated the input value or output value
  private didInputChangedRecently = true;

  // Subject which defines if all the data is loaded or not
  initialDataLoaded$: BehaviorSubject<boolean> = new BehaviorSubject(false);

  // Subject which holds the exchanges selected by the user
  selectedExchanges$: BehaviorSubject<Exchange[]> = new BehaviorSubject([]);

  // Fired when the output is calculated
  outputCalculated$: BehaviorSubject<string | number> = new BehaviorSubject(
    '1'
  );

  // Fired when the input is calculated
  inputCalculated$: BehaviorSubject<string | number> = new BehaviorSubject(
    '1,000,000'
  );

  // Form model data with defaults
  model = {
    inputValue: '1,000,000',
    outputValue: '1',
    inputCurrency: 'CSC',
    outputCurrency: 'USD'
  };

  // Subscriptions holding the latest data from remote services
  btcPrice$: BehaviorSubject<Btc> = new BehaviorSubject(new Btc());
  currencies$: BehaviorSubject<Currency[]> = new BehaviorSubject([]);
  exchanges$: BehaviorSubject<Exchange[]> = new BehaviorSubject([]);

  constructor(
    private currencyService: CurrencyService,
    private exchangeService: ExchangeService,
    private btcPriceService: BtcPriceService
  ) {
    const resourcePollingJob = this.createResourcePolling(6000);
    resourcePollingJob.subscribe();

    this.selectedExchanges$.subscribe(() => {
      if (this.selectedExchanges$.value.length > 0) {
        this.updateInputOrOutput();
      }
    });
  }

  createResourcePolling(delayTime: number) {
    return forkJoin(
      this.currencyService.getAll(),
      this.exchangeService.getAll(),
      this.btcPriceService.currentPrice()
    ).pipe(
      tap(responses => this.remoteDataUpdated(responses)),
      delay(delayTime),
      repeat()
    );
  }

  remoteDataUpdated(responses) {
    // Update the data
    this.currencies$.next(responses[0]);
    this.exchanges$.next(responses[1]);
    this.btcPrice$.next(responses[2]);

    // Initialize the selectedExchanges$ only once
    if (this.selectedExchanges$.value.length === 0) {
      this.selectedExchanges$.next(responses[1]);
    }

    this.updateInputOrOutput();

    // Fire the event and mark the subscription as complete
    this.initialDataLoaded$.next(true);
    this.initialDataLoaded$.complete();
  }

  updateInputOrOutput() {
    if (this.didInputChangedRecently) {
      this.inputUpdated({});
    } else {
      this.outputUpdated({});
    }
  }

  inputUpdated(model) {
    Object.assign(this.model, ...model);
    this.didInputChangedRecently = true;

    const input = this.model.inputValue.toString().replace(/\D/g, '');

    let output = '';
    if (!!input) {
      output = this.btcPrice$.value
        .inSatoshis()
        .multipliedBy(this.priceOfCsc())
        .multipliedBy(input)
        .multipliedBy(this.currencyConversionFactor())
        .toFormat(4);
    }

    this.model.outputValue = output;
    this.outputCalculated$.next(output);
  }

  outputUpdated(model) {
    Object.assign(this.model, ...model);
    this.didInputChangedRecently = false;

    const cleanOutput = this.model.outputValue.replace(/\D/g, '');
    let output = '';
    if (!!cleanOutput) {
      const outputInDollars = new BigNumber(cleanOutput).dividedBy(
        this.currencyConversionFactor()
      );
      const btcs = outputInDollars.dividedBy(this.btcPrice$.value.price);
      const satoshis = btcs.multipliedBy(ConversionService.BTC_TO_SATOSHI);
      const cscs = satoshis.dividedBy(this.priceOfCsc());

      output = cscs.toFormat(0);
    }
    this.model.inputValue = output;
    this.inputCalculated$.next(output);
  }

  priceOfCsc(): BigNumber {
    const prices = this.selectedExchanges$.value.map(_ =>
      _.last.multipliedBy(ConversionService.BTC_TO_SATOSHI).toNumber()
    );
    return new BigNumber(
      prices.reduce((accumulator, currentValue) => accumulator + currentValue)
    ).dividedBy(prices.length);
  }

  currencyConversionFactor(): number {
    return this.currencies$.value.find(
      _ => _.symbol === this.model.outputCurrency
    ).value;
  }

  // dropsToCSC(drops): number {
  //   return drops.dividedBy(ConversionService.CSC_TO_DROP);
  // }
}
