import { CurrencyService } from '../../../../data/currency.service';
import { Currency } from '../../../../data/currency';
import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { Observable } from 'rxjs';
import { FormGroup, FormBuilder } from '@angular/forms';
import { filter, tap, debounceTime, merge } from 'rxjs/operators';
import { ConversionService } from 'src/app/data/conversion.service';

@Component({
  selector: 'app-currency-converter',
  templateUrl: './currency-converter.component.html',
  styleUrls: ['./currency-converter.component.scss']
})
export class CurrencyConverterComponent implements OnInit {
  constructor(
    public conversionService: ConversionService,
    private currencyService: CurrencyService,
    private fb: FormBuilder
  ) {}
  currencies$: Observable<Currency[]>;
  converterForm: FormGroup;
  @ViewChild('inputValue') inputValue;
  @ViewChild('outputValue') outputValue;

  // Helper method for changing the styles of the input elements
  static restyleInputElements(
    changeElement: ElementRef,
    resetElement: ElementRef,
    changeValue: string | number
  ) {
    // Change width and font weight
    if (changeElement && changeElement.nativeElement) {
      const pixels = Math.max((changeValue.toString().length + 1) * 32, 64);
      const el = changeElement.nativeElement;
      el.style.width = `${pixels}px`;
      el.style.fontWeight = 'bold';
    }

    // Reset the font to normal
    if (resetElement && resetElement.nativeElement) {
      resetElement.nativeElement.style.fontWeight = 'normal';
    }
  }

  ngOnInit() {
    this.currencies$ = this.conversionService.currencies$;

    // Create reactive form
    this.converterForm = this.initForm();

    // Set the initial values to the form
    this.converterForm.patchValue(this.conversionService.model);

    this.setupInputElementsChangeListener();
    this.setupOutputElementsChangeListener();
    this.setupDataChangeListener();
  }

  initForm() {
    return this.fb.group({
      inputValue: [],
      inputCurrency: [],
      outputValue: [],
      outputCurrency: []
    });
  }

  setupInputElementsChangeListener() {
    this.converterForm.controls.inputValue.valueChanges
      .pipe(
        merge(this.converterForm.controls.inputCurrency.valueChanges),
        merge(this.converterForm.controls.outputCurrency.valueChanges),
        filter(() => this.converterForm.valid),
        tap(() => {
          CurrencyConverterComponent.restyleInputElements(
            this.inputValue,
            this.outputValue,
            this.converterForm.controls.inputValue.value
          );
        }),
        debounceTime(100),
        tap(() => {
          this.conversionService.inputUpdated(this.converterForm.value);
        })
      )
      .subscribe();
  }

  setupOutputElementsChangeListener() {
    this.converterForm.controls.outputValue.valueChanges
      .pipe(
        filter(() => this.converterForm.valid),
        tap(() => {
          CurrencyConverterComponent.restyleInputElements(
            this.outputValue,
            this.inputValue,
            this.converterForm.controls.outputValue.value
          );
        }),
        debounceTime(100),
        tap(() => {
          this.conversionService.outputUpdated(this.converterForm.value);
        })
      )
      .subscribe();
  }

  setupDataChangeListener() {
    // When the output value is changed update the UI and style the components
    this.conversionService.outputCalculated$.subscribe(value => {
      this.converterForm.controls.outputValue.setValue(value, {
        emitEvent: false
      });
      CurrencyConverterComponent.restyleInputElements(
        this.outputValue,
        this.inputValue,
        value
      );
    });

    // When the input value is changed update the UI and style the components
    this.conversionService.inputCalculated$.subscribe(value => {
      this.converterForm.controls.inputValue.setValue(value, {
        emitEvent: false
      });
      CurrencyConverterComponent.restyleInputElements(
        this.inputValue,
        this.outputValue,
        value
      );
    });
  }
}
