import BigNumber from 'bignumber.js';

export class Exchange {
  id: string;
  name: string;
  image: string;
  buy: BigNumber;
  sell: BigNumber;
  last: BigNumber;
  volume24H: number;
  creationDate: Date;
  lastUpdateDate: Date;
  endpoint: string;
  enabled: boolean;
  tradeURL: string;

  deserialize(item: any) {
    this.id = item._id;
    this.name = item.name;
    this.image = item.image;
    this.buy = new BigNumber(item.buy);
    this.sell = new BigNumber(item.sell);
    this.last = new BigNumber(item.last);
    this.volume24H = item.volume24H;
    this.creationDate = new Date(item.creationDate);
    this.lastUpdateDate = new Date(item.lastUpdateDate);
    this.endpoint = item.endpoint;
    this.enabled = item.enabled;
    this.tradeURL = item.tradeURL;
    return this;
  }
}
