import BigNumber from 'bignumber.js';

export class Btc {
  static SATOSHIS_PER_BTC = new BigNumber(100000000); // 100,000,000
  price: BigNumber;

  deserialize(price) {
    this.price = new BigNumber(price);
    return this;
  }

  inSatoshis(): BigNumber {
    return this.price.dividedBy(Btc.SATOSHIS_PER_BTC);
  }
}
